body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body {
  background-color: var(--bg);
  color: var(--textNormal);
}
h1,
h2,
h3 {
  color: var(--header);
}
body.light {
  --bg: #ffffff;
  --header: rgb(27, 86, 121);
  --textNormal: #222;
  --textTitle: #222;
  --textDisabled: #8b8686;
  --textLink: #0d959e;
  --hr: hsla(0, 0%, 0%, 0.2);
  --inlineCode-bg: rgba(255, 229, 100, 0.2);
  --inlineCode-text: #1a1a1a;
  --bgFooter: #eee;
}

body.dark {
  -webkit-font-smoothing: antialiased;
  --bg: #282c35;
  --header: #0d959e;
  --textNormal: rgba(255, 255, 255, 0.88);
  --textTitle: #ffffff;
  --textDisabled: #8b8686;
  --textLink: #61dafb;
  --hr: hsla(0, 0%, 100%, 0.2);
  --inlineCode-bg: hsl(222, 14%, 25%);
  --inlineCode-text: #e6e6e6;
  --bgFooter: #121417;
}
header {
  color: var(--header);
  text-align: center;
}
div {
  color: var(--textNormal);
}
button {
  color: var(--textTitle);
}
button.disabled {
  color: var(--textDisabled);
}
.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  line-height: 1.5;
}
main {
  flex: 1 1;
  margin-left: auto;
  margin-right: auto;
  max-width: 42rem;
  padding: 0.625rem 1.3125rem;
}
a {
  color: var(--textLink);
  text-decoration: none;
}
ul li {
  list-style-type: none;
  list-style-image: url(/blog/static/media/star_list.cbf3350d.svg);
  list-style-position: inside;
}

article {
  text-align: left;
}
@media screen and (min-width: 700px) {
  article {
    text-align: justify;
  }
}
@media screen and (min-width: 1100px) {
  article {
    text-align: justify;
  }
  .wrapper {
    font-size: calc(12px + 1vmin);
  }
}
footer {
  width: 100%;
  background-color: var(--bgFooter);
  padding: 0.5rem 0;
  margin-top: 2%;
}
#copyright {
  font-size: 90%;
}
#contactIcons a {
  padding: 0em 0.4em;
  font-size: 1.5rem;
}
.icon-mail4 {
  color: #d54a3d;
}
#contactIcons a#linkedIn i {
  color: #007bb6;
}
#contactIcons a#github i {
  color: #444444;
}
#contactIcons a#facebook i {
  color: #3b5998;
}
#contactIcons a#twitter i {
  color: #1da1f2;
}
.postFooter {
  font-size: 90%;
}
#snackbar {
  visibility: hidden;
  min-width: 250px;
  margin-left: -125px;
  background-color: #333;
  color: #fff;
  text-align: center;
  border-radius: 2px;
  padding: 16px;
  position: fixed;
  z-index: 1;
  left: 50%;
  bottom: 30px;
  font-size: 17px;
}

#snackbar.show {
  visibility: visible;
  -webkit-animation: fadein 0.5s, fadeout 0.5s 2.5s;
  animation: fadein 0.5s, fadeout 0.5s 2.5s;
}

@-webkit-keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@-webkit-keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}

@keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}

@font-face {
  font-family: 'icomoon';
  src:  url(/blog/static/media/icomoon.bba726b6.eot);
  src:  url(/blog/static/media/icomoon.bba726b6.eot#iefix) format('embedded-opentype'),
    url(/blog/static/media/icomoon.27486e85.ttf) format('truetype'),
    url(/blog/static/media/icomoon.b685eba4.woff) format('woff'),
    url(/blog/static/media/icomoon.2a0cc897.svg#icomoon) format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  -webkit-font-feature-settings: normal;
          font-feature-settings: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

